:root {
  --PrimaryColor: hsl(199, 100%, 33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --gradientColor: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(71,71,107,1) 18%, rgba(101,101,130,1) 25%, rgba(7,7,102,1) 27%, rgba(0,212,255,1) 100%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(201, 33%, 16%);
  --textColor: hsl(240, 4%, 36%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greatText: rgb(190, 190, 190);
  --inputColor: rgb(239, 239, 239);
  --bodyColor: rgb(240, 240, 246);
  --cardBG: rgb(225, 225, 235);
}

.home {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.home .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(47, 106, 127);
  top: 0;
  bottom: 0;
  z-index: -1;
  mix-blend-mode: hard-light;
}
.home video {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  -o-object-fit: cover;
     object-fit: cover;
}
.home .homeContent {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  padding: 8rem 1.5rem;
  display: flex;
  row-gap: 3rem;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  margin: auto;
  color: var(--whiteColor);
  z-index: 100;
}
.home .homeContent .textDiv {
  padding: 2rem 0;
  text-align: center;
  justify-content: center;
}
.home .homeContent .textDiv .smallText {
  font-size: 35px;
  text-transform: uppercase;
  color: var(--whiteColorDeam);
}
.home .homeContent .textDiv .homeTitle {
  position: relative;
  font-size: 1.8rem;
  color: var(--whiteColorDeam);
}
.home .homeContent .cardDiv {
  position: relative;
  padding: 2rem 1rem;
  gap: 1rem;
  border-radius: 5%;
}
.home .homeContent .cardDiv label {
  display: block;
  color: var(--whiteColor);
  padding-bottom: 10px;
  font-size: 20px;
  font-weight: 500;
}
.home .homeContent .cardDiv .label_total {
  justify-content: space-between;
  align-items: center;
}
.home .homeContent .cardDiv .label_total .total {
  color: var(--whiteColor);
  font-size: 18px;
  padding-bottom: 10px;
}
.home .homeContent .cardDiv .input {
  height: 60px;
  padding: 0 1rem;
  background: transparent;
  border-radius: 3rem;
  border: 1px solid var(--whiteColor);
}
.home .homeContent .cardDiv .input input {
  background: none;
  color: var(--whiteColor);
  border: none;
  outline: none;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
}
.home .homeContent .cardDiv .input input::-moz-placeholder {
  font-size: 18px;
  color: white;
}
.home .homeContent .cardDiv .input input::placeholder {
  font-size: 18px;
  color: white;
}
.home .homeContent .cardDiv .input .icon {
  font-size: 30px;
  color: var(--whiteColor) !important;
  margin-left: 10px;
}
.home .homeContent .cardDiv .input .input[type=range] {
  background: blue;
}
.home .homeContent .searchOptions {
  position: absolute;
  background: transparent;
  width: -moz-max-content;
  width: max-content;
  height: 100px;
  -moz-column-gap: 0.8rem;
       column-gap: 0.8rem;
  padding: 5px 1rem;
  border-radius: 3rem;
  border: 1px solid var(--whiteColor);
  left: 50%;
  transform: translate(-50%);
  justify-content: center;
  cursor: pointer;
}
.home .homeContent .searchOptions:hover {
  background: var(--textColor);
}
.home .homeContent .searchOptions .icon {
  font-size: 30px;
}
.home .homeContent .searchOptions span {
  font-size: 18px;
}
.home .homeContent .homeFooterIcons {
  width: 100%;
  justify-content: space-between;
}
.home .homeContent .homeFooterIcons .icon {
  font-size: 20px;
  margin: 0 5px;
  color: var(--whiteColor);
  font-weight: 600;
}
.home .homeContent .homeFooterIcons .icon:hover {
  color: var(--PrimaryColor);
}

@media screen and (min-width: 500px) {
  .home .homeContent .searchOptions {
    bottom: 4rem;
    height: 50px;
  }
  .home .homeContent .searchOptions .icon {
    font-size: 15px;
  }
  .home .homeContent .searchOptions span {
    font-size: 10px;
  }
  .home .homeContent .textDiv .homeTitle {
    position: relative;
  }
  .home .cardDiv {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 760px) {
  .home .homeContent .cardDiv {
    padding: 2rem 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .home .homeContent .searchOptions {
    bottom: 4rem;
    height: 50px;
  }
  .home .homeContent .searchOptions .icon {
    font-size: 15px;
  }
  .home .homeContent .searchOptions span {
    font-size: 10px;
  }
}
@media screen and (min-width: 940px) {
  .home {
    height: 70vh;
  }
  .home .homeContent {
    padding-left: 0;
    padding-right: 0;
  }
  .home .homeContent .searchOptions {
    bottom: 4rem;
    height: 45px;
  }
  .home .homeContent .searchOptions .icon {
    font-size: 25px;
  }
  .home .homeContent .searchOptions span {
    font-size: 10px;
  }
}
@media screen and (min-width: 1140px) {
  .home {
    height: 95vh;
    padding: 3rem;
  }
  .home .homeContent {
    padding-left: 0;
    padding-right: 0;
  }
  .home .homeContent .searchOptions {
    bottom: 7rem;
    height: 55px;
  }
  .home .homeContent .searchOptions .icon {
    font-size: 30px;
  }
  .home .homeContent .searchOptions span {
    font-size: 20px;
  }
  .home .homeContent .cardDiv {
    padding: 2rem 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .home .homeContent .cardDiv .input {
    margin-bottom: 1rem;
  }
  .home .homeContent .cardDiv .searchOptions {
    bottom: -1rem;
    padding: 8px 1rem;
  }
  .home .homeContent .cardDiv .searchOptions span {
    font-size: 18px;
  }
  .home .homeContent .homeFooterIcons .icon {
    font-size: 40px;
  }
}
@media screen and (min-width: 2000px) {
  .home {
    height: 95vh;
  }
  .home .homeContent .cardDiv {
    grid-template-columns: repeat(2, 1fr);
    background-color: none;
  }
  .home .homeContent .searchOptions {
    bottom: 20rem;
    height: 60px;
  }
  .home .homeContent .searchOptions .icon {
    font-size: 40px;
  }
  .home .homeContent .searchOptions span {
    font-size: 25px;
  }
  .home .homeContent .textDiv .smallText {
    font-size: 100px;
  }
  .home .homeContent .textDiv .homeTitle {
    font-size: 50px;
  }
  .home .homeContent .homeFooterIcons .icon {
    font-size: 35px;
  }
}/*# sourceMappingURL=home.css.map */