:root{
    --PrimaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient(to-right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greatText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG:  rgb(225, 225, 235);
    --cardHover:  rgb(255, 255, 255);
  }

  h2 {
    padding-bottom: 1rem;
  }

  .container {
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding: 5rem;
    
    .mainIconsInfor {
        width: 100%;
        height: 100%;
        flex-direction: row;
        align-items: flex-end;
       

        .masterMainIcons{
            flex-direction: column;

            .mainIcons{
                span{
                    padding-right: 2rem;
                    text-align:left;
                }

                a{
                    text-align:justify;
                }
            }
        }
    }
}


@media screen and (min-width: 1240px) {
    .container {
        width: 100%;
        justify-content: center;

        .mainIconsInfor {
            width: 100%;
            justify-content: center;

            .mainIcons{
                span{
                    padding-right: 5rem;
                    font-size: 20px;
                }

                a{
                    text-align:justify;
                    font-size: 20px;
                }
            }
        }
    }
}

@media screen and (min-width: 2000px) {
    .container {
        width: 100%;
        justify-content: center;

        .mainIconsInfor {
            width: 100%;
            justify-content: center;

            .mainIcons{
                span{
                    padding-right: 5rem;
                    font-size: 25px;
                }

                a{
                    text-align:end;
                    font-size: 25px;
                }
            }
        }
    }
}