:root{
    --PrimaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(71,71,107,1) 18%, rgba(101,101,130,1) 25%, rgba(7,7,102,1) 27%, rgba(0,212,255,1) 100%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greatText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG:  rgb(225, 225, 235);
  }

  .footer{
    width: 100%;
    position: relative;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;

    .videoDiv{
        position: absolute;
        height: 100%;
        width: 100%;
        margin-top: 5rem;

        video{
            height: 100%;
            object-fit: cover;
        }

        &::after{
            content: '';
            position: absolute;
            background: rgba(10, 84, 107, 0.307);
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            mix-blend-mode: multiply;
        }
    }

    .secContent{
        width: 100%;
        height: max-content;
        padding: 2rem initial;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: auto;
        row-gap: 2rem;
        z-index: 100;

        .contactDiv{
            justify-content: space-between;
            flex-direction: column;
            gap: 1rem;
            align-items: center;
            justify-content: center;
            text-align: center;
            margin-top: 2.5rem;
            .text{
                small{
                    font-size: 20px;
                    font-weight: 400;
                    color: var(--whiteColor);
                }

                h2{
                    color: var(--whiteColor);
                    font-size: 1.8rem;
                }
            }

            .inputDiv{
                width: 100%;
                gap: 1rem;
                flex-direction: column;

                input{
                    width: 100%;
                    padding: .5rem .6rem;
                    border-radius: 3rem;
                    border: 1px solid var(--whiteColor);
                    outline: none;
                    background: transparent;
                    color: var(--whiteColor);

                    &::placeholder{
                        color: var(--whiteColor);
                        opacity: .5;
                    }
                }

                .btn {
                    width: 100%;
                    justify-content: center;
                    background-color: var(--textColor);
                    color: var(--whiteColor);
                    font-weight: 500;
                    gap: .5rem;

                    .icon{
                        font-size: 18px;
                    }
                }
            }
        }

        .footerCard{
            position: relative;
            padding: 1rem 1rem 4rem;
            gap: 2rem;
            border-radius: 1rem;
            background: transparent;
            width: 100%;
            justify-content: center;
            flex-direction: column;
            align-items: flex-start;
            overflow: hidden;

            .footerIntro{
                flex-basis: 50%;
                justify-content: center;
                align-items: flex-start;
                flex-direction: column;
                row-gap: .5rem;
                padding-top: 1.7rem;

                .footerParagraph{
                    a{
                        font-size: 17px;
                        color: var(--whiteColorDeam);
                    }
                    
                }
    
                .logoDiv{
                    a {
                        font-size: 20px;
                    }
                    .logo{
                        color: var(--whiteColorDeam);
                        font-weight: 600;
    
                        .icon{
                            font-size: 25px;
                            color: var(--whiteColorDeam);
                            margin-right: 10px;
                        }
                    }
                }
    
                .footerSocialss{
                    column-gap: .5rem;
    
                    .icon{
                        color: var(--whiteColorDeam);
                        font-size: 30px;
                        padding: 0.2rem;
    
                        &:hover{
                            color: var(--PrimaryColor);
                        }
                    }
                }
            }

            .masterFooterLinks{
                display: flex;
                flex-direction: column;
                width: 100%;
                height: 100%;
                justify-content: center;
                
                h2{
                    text-align: center;
                    margin-bottom: 1rem;
                    color: var(--whiteColorDeam);
                }

                .footerLinks{
                    flex-basis: 50%;
                    width: 100%;
                    gap: 1rem;
                    justify-content: space-between;
                    font-size: 20px;


                    .linkGroup{
                        .groupTitle{
                            display: flex;
                            font-weight: 600;
                            color: var(--whiteColorDeam);
                            margin-bottom: .5rem;
                            justify-content: center;
                        }

                        .footerList{
                            font-size: 15px;
                            color: var(--whiteColorDeam);
                            transition: .3s ease-in-out;
                            cursor: pointer;
                            justify-content: center;
                            font-size: 18px;

                            .icon{
                                font-size: 18px;
                                color: grey;
                            }

                            &:hover{
                                color: var(--PrimaryColor);
                                transform: translateX(7px);

                                .icon{
                                    color: var(--SecondaryColor);
                                }
                            }
                        }
                    }
                }
            }

            .footerDiv{
                flex-direction: column;
                position: absolute;
                width: 100%;
                bottom: 0;
                left: 0;
                right: 0;
                background: var(--textColor);
                padding: .5rem 2rem;
                justify-content: center;
                align-self: center;

                small{
                    font-size: 15px;
                    color: var(--whiteColor);
                }
            }
        }
    }
  }

// Media Queries =============>

@media screen and (min-width:500px) {
    .footer{
        .secContent {
            .footerCard{
                .footerLinks{
                    grid-template-columns: repeat(1, 1fr);
                    padding-bottom: 1rem;
                }

                .footerDiv{
                    justify-content: space-between;
                    flex-direction: row;

                    small {
                        font-size: 14px;
                        color: var(--whiteColor);
                    }
                }
            }
        }
    }
}

@media screen and (min-width:660px) {
    .footer{
        .secContent {

            .footerCard{
                .footerLinks{
                    grid-template-columns: repeat(2, 1fr);
                    padding-bottom: 1rem;
                }
            }
            .contactDiv {
                flex-direction: row;
                justify-content: space-between;

                .inputDiv {
                    width: max-content;
                    flex-direction: row;
                    justify-content: flex-start;

                    input{
                        width: 70%;
                    }

                    .btn {
                        width: max-content;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 840px) {
    .footer{
        padding: 5rem 0;
        .secContent {
            .footerCard{
                    flex-direction: row;
        
                    .footerLinks{
                        grid-template-columns: repeat(3,1fr);
                        padding-bottom: 1rem;
                    } 
            }
            
        }
    }
}

@media screen and (min-width: 1024px) {
    .footer{
        .secContent{
            .footerCard{
                padding: 3rem 1rem;

                .footerIntro {
                    padding-top: 3.7rem;

                    .footerParagraph {
                        font-size: 15px;
                        color: var(--textColor);
                    }

                    .footerSocials {
                        column-gap: .5rem;
                        .icon{
                            color: var(--textColor);
                            font-size: 25px;
                            &:hover{
                                color: var(--PrimaryColor);
                            }

                        }
                    }
                }

                .masterFooterLinks{
                    font-size: 16px;
                    padding: 5px 0;
                }
            }
        }
    }
}


@media screen and (min-width: 2000px) {
    .footer{
        padding: 0rem ;

        .secContent {
            .contactDiv {
                padding-top: 1.5rem;
                .text {
                    small {
                        padding: 10rem;
                        font-size: 25px
                    }
                }
            }
        }
    }
};
    
