:root {
  --PrimaryColor: hsl(199, 100%, 33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --gradientColor: linear-gradient(to-right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(201, 33%, 16%);
  --textColor: hsl(240, 4%, 36%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greatText: rgb(190, 190, 190);
  --inputColor: rgb(239, 239, 239);
  --bodyColor: rgb(240, 240, 246);
  --cardBG: rgb(225, 225, 235);
  --cardHover: rgb(255, 255, 255);
}

.main {
  width: 100%;
}
.main .secTitle .title {
  position: relative;
  width: -moz-max-content;
  width: max-content;
  color: var(--textColor);
  margin: 1rem 0;
  z-index: 2;
}
.main .secContent {
  justify-content: center;
  gap: 2rem;
}
.main .secContent .singleService {
  height: 100%;
  display: grid;
  row-gap: 10px;
  border-radius: 20px;
  align-items: center;
  background: var(--cardBG);
  box-shadow: 0 2px 4px rgba(140, 140, 141, 0.549);
  overflow: hidden;
}
.main .secContent .singleService:hover {
  background: var(--cardHover);
  box-shadow: 1 4px 4px rgba(85, 85, 114, 0.549);
  transition: 0.3s ease;
}
.main .secContent .singleService .imageDiv {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.main .secContent .singleService .imageDiv img {
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  transition: 2s ease;
}
.main .secContent .singleService .imageDiv:hover img {
  transform: scale(1.2);
}
.main .secContent .singleService .cardInfo {
  padding: 1rem;
}
.main .secContent .singleService .cardInfo .SerTitle {
  color: black;
  font-size: 1.5rem;
}
.main .secContent .singleService .cardInfo .continent {
  gap: 0.5rem;
}
.main .secContent .singleService .cardInfo .continent .icon {
  color: var(--textColor);
  font-size: 25px;
}
.main .secContent .singleService .cardInfo .continent .name {
  color: var(--textColor);
  font-weight: 400;
  font-size: 18px;
}
.main .secContent .singleService .cardInfo .fees {
  width: 100%;
  justify-content: space-between;
  padding: 1rem;
  margin: 1rem 0;
  border-top: 1.5px solid var(--greatText);
  border-bottom: 1.5px solid var(--greatText);
}
.main .secContent .singleService .cardInfo .fees .grade {
  color: var(--textColor);
  max-width: 100px;
  line-height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main .secContent .singleService .cardInfo .fees .grade small {
  text-align: center;
  background: var(--textColor);
  border-radius: 1rem;
  color: var(--whiteColor);
  padding: 1px 10px;
  font-size: 10px;
}
.main .secContent .singleService .cardInfo .fees .price {
  font-size: 2rem;
  color: var(--blackColor);
}
.main .secContent .singleService .cardInfo .desc {
  font-size: 20px;
  color: var(--textColor);
  font-weight: 500;
}
.main .secContent .singleService .cardInfo .btn {
  margin-top: 1rem;
  color: var(--whiteColor);
  font-weight: 600;
  justify-content: space-around;
  gap: 0.5rem;
}
.main .secContent .singleService .cardInfo .btn .icon {
  align-self: center;
  font-size: 18px;
}

@media screen and (min-width: 500px) {
  .title {
    font-size: 1.5rem;
  }
  .title::after {
    width: 155px !important;
  }
  .secContent {
    grid-template-columns: repeat(1, 1fr);
  }
  .secContent .singleService {
    height: auto;
  }
}
@media screen and (min-width: 840px) {
  .secContent {
    grid-template-columns: repeat(2, 1fr);
  }
  .secContent .singleService {
    height: auto;
  }
}
@media screen and (min-width: 1240px) {
  .secContent {
    grid-template-columns: repeat(3, 1fr);
  }
  .secContent .singleService {
    height: auto;
  }
}
@media screen and (min-width: 2000px) {
  .secContent {
    grid-template-columns: repeat(4, 1fr);
  }
  .secContent .singleService {
    height: auto;
  }
}/*# sourceMappingURL=main.css.map */