:root {
  --PrimaryColor: hsl(199, 100%, 33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --gradientColor: linear-gradient(to-right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(201, 33%, 16%);
  --textColor: hsl(240, 4%, 36%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greatText: rgb(190, 190, 190);
  --inputColor: rgb(239, 239, 239);
  --bodyColor: rgb(240, 240, 246);
  --cardBG: rgb(225, 225, 235);
  --cardHover: rgb(255, 255, 255);
}

h2 {
  padding-bottom: 1rem;
}

.container {
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding: 5rem;
}
.container .mainIconsInfor {
  width: 100%;
  height: 100%;
  flex-direction: row;
  align-items: flex-end;
}
.container .mainIconsInfor .masterMainIcons {
  flex-direction: column;
}
.container .mainIconsInfor .masterMainIcons .mainIcons span {
  padding-right: 2rem;
  text-align: left;
}
.container .mainIconsInfor .masterMainIcons .mainIcons a {
  text-align: justify;
}

@media screen and (min-width: 1240px) {
  .container {
    width: 100%;
    justify-content: center;
  }
  .container .mainIconsInfor {
    width: 100%;
    justify-content: center;
  }
  .container .mainIconsInfor .mainIcons span {
    padding-right: 5rem;
    font-size: 20px;
  }
  .container .mainIconsInfor .mainIcons a {
    text-align: justify;
    font-size: 20px;
  }
}
@media screen and (min-width: 2000px) {
  .container {
    width: 100%;
    justify-content: center;
  }
  .container .mainIconsInfor {
    width: 100%;
    justify-content: center;
  }
  .container .mainIconsInfor .mainIcons span {
    padding-right: 5rem;
    font-size: 25px;
  }
  .container .mainIconsInfor .mainIcons a {
    text-align: end;
    font-size: 25px;
  }
