:root {
  --PrimaryColor: hsl(199, 100%, 33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --gradientColor: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(71,71,107,1) 18%, rgba(101,101,130,1) 25%, rgba(7,7,102,1) 27%, rgba(0,212,255,1) 100%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(201, 33%, 16%);
  --textColor: hsl(240, 4%, 36%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greatText: rgb(190, 190, 190);
  --inputColor: rgb(239, 239, 239);
  --bodyColor: rgb(240, 240, 246);
  --cardBG: rgb(225, 225, 235);
}

.footer {
  width: 100%;
  position: relative;
  padding: 2rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.footer .videoDiv {
  position: absolute;
  height: 100%;
  width: 100%;
  margin-top: 5rem;
}
.footer .videoDiv video {
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.footer .videoDiv::after {
  content: "";
  position: absolute;
  background: rgba(10, 84, 107, 0.307);
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  mix-blend-mode: multiply;
}
.footer .secContent {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  padding: 2rem initial;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  row-gap: 2rem;
  z-index: 100;
}
.footer .secContent .contactDiv {
  justify-content: space-between;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 2.5rem;
}
.footer .secContent .contactDiv .text small {
  font-size: 20px;
  font-weight: 400;
  color: var(--whiteColor);
}
.footer .secContent .contactDiv .text h2 {
  color: var(--whiteColor);
  font-size: 1.8rem;
}
.footer .secContent .contactDiv .inputDiv {
  width: 100%;
  gap: 1rem;
  flex-direction: column;
}
.footer .secContent .contactDiv .inputDiv input {
  width: 100%;
  padding: 0.5rem 0.6rem;
  border-radius: 3rem;
  border: 1px solid var(--whiteColor);
  outline: none;
  background: transparent;
  color: var(--whiteColor);
}
.footer .secContent .contactDiv .inputDiv input::-moz-placeholder {
  color: var(--whiteColor);
  opacity: 0.5;
}
.footer .secContent .contactDiv .inputDiv input::placeholder {
  color: var(--whiteColor);
  opacity: 0.5;
}
.footer .secContent .contactDiv .inputDiv .btn {
  width: 100%;
  justify-content: center;
  background-color: var(--textColor);
  color: var(--whiteColor);
  font-weight: 500;
  gap: 0.5rem;
}
.footer .secContent .contactDiv .inputDiv .btn .icon {
  font-size: 18px;
}
.footer .secContent .footerCard {
  position: relative;
  padding: 1rem 1rem 4rem;
  gap: 2rem;
  border-radius: 1rem;
  background: transparent;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;
}
.footer .secContent .footerCard .footerIntro {
  flex-basis: 50%;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  row-gap: 0.5rem;
  padding-top: 1.7rem;
}
.footer .secContent .footerCard .footerIntro .footerParagraph a {
  font-size: 17px;
  color: var(--whiteColorDeam);
}
.footer .secContent .footerCard .footerIntro .logoDiv a {
  font-size: 20px;
}
.footer .secContent .footerCard .footerIntro .logoDiv .logo {
  color: var(--whiteColorDeam);
  font-weight: 600;
}
.footer .secContent .footerCard .footerIntro .logoDiv .logo .icon {
  font-size: 25px;
  color: var(--whiteColorDeam);
  margin-right: 10px;
}
.footer .secContent .footerCard .footerIntro .footerSocialss {
  -moz-column-gap: 0.5rem;
       column-gap: 0.5rem;
}
.footer .secContent .footerCard .footerIntro .footerSocialss .icon {
  color: var(--whiteColorDeam);
  font-size: 30px;
  padding: 0.2rem;
}
.footer .secContent .footerCard .footerIntro .footerSocialss .icon:hover {
  color: var(--PrimaryColor);
}
.footer .secContent .footerCard .masterFooterLinks {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
}
.footer .secContent .footerCard .masterFooterLinks h2 {
  text-align: center;
  margin-bottom: 1rem;
  color: var(--whiteColorDeam);
}
.footer .secContent .footerCard .masterFooterLinks .footerLinks {
  flex-basis: 50%;
  width: 100%;
  gap: 1rem;
  justify-content: space-between;
  font-size: 20px;
}
.footer .secContent .footerCard .masterFooterLinks .footerLinks .linkGroup .groupTitle {
  display: flex;
  font-weight: 600;
  color: var(--whiteColorDeam);
  margin-bottom: 0.5rem;
  justify-content: center;
}
.footer .secContent .footerCard .masterFooterLinks .footerLinks .linkGroup .footerList {
  font-size: 15px;
  color: var(--whiteColorDeam);
  transition: 0.3s ease-in-out;
  cursor: pointer;
  justify-content: center;
  font-size: 18px;
}
.footer .secContent .footerCard .masterFooterLinks .footerLinks .linkGroup .footerList .icon {
  font-size: 18px;
  color: grey;
}
.footer .secContent .footerCard .masterFooterLinks .footerLinks .linkGroup .footerList:hover {
  color: var(--PrimaryColor);
  transform: translateX(7px);
}
.footer .secContent .footerCard .masterFooterLinks .footerLinks .linkGroup .footerList:hover .icon {
  color: var(--SecondaryColor);
}
.footer .secContent .footerCard .footerDiv {
  flex-direction: column;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--textColor);
  padding: 0.5rem 2rem;
  justify-content: center;
  align-self: center;
}
.footer .secContent .footerCard .footerDiv small {
  font-size: 15px;
  color: var(--whiteColor);
}

@media screen and (min-width: 500px) {
  .footer .secContent .footerCard .footerLinks {
    grid-template-columns: repeat(1, 1fr);
    padding-bottom: 1rem;
  }
  .footer .secContent .footerCard .footerDiv {
    justify-content: space-between;
    flex-direction: row;
  }
  .footer .secContent .footerCard .footerDiv small {
    font-size: 14px;
    color: var(--whiteColor);
  }
}
@media screen and (min-width: 660px) {
  .footer .secContent .footerCard .footerLinks {
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 1rem;
  }
  .footer .secContent .contactDiv {
    flex-direction: row;
    justify-content: space-between;
  }
  .footer .secContent .contactDiv .inputDiv {
    width: -moz-max-content;
    width: max-content;
    flex-direction: row;
    justify-content: flex-start;
  }
  .footer .secContent .contactDiv .inputDiv input {
    width: 70%;
  }
  .footer .secContent .contactDiv .inputDiv .btn {
    width: -moz-max-content;
    width: max-content;
  }
}
@media screen and (min-width: 840px) {
  .footer {
    padding: 5rem 0;
  }
  .footer .secContent .footerCard {
    flex-direction: row;
  }
  .footer .secContent .footerCard .footerLinks {
    grid-template-columns: repeat(3, 1fr);
    padding-bottom: 1rem;
  }
}
@media screen and (min-width: 1024px) {
  .footer .secContent .footerCard {
    padding: 3rem 1rem;
  }
  .footer .secContent .footerCard .footerIntro {
    padding-top: 3.7rem;
  }
  .footer .secContent .footerCard .footerIntro .footerParagraph {
    font-size: 15px;
    color: var(--textColor);
  }
  .footer .secContent .footerCard .footerIntro .footerSocials {
    -moz-column-gap: 0.5rem;
         column-gap: 0.5rem;
  }
  .footer .secContent .footerCard .footerIntro .footerSocials .icon {
    color: var(--textColor);
    font-size: 25px;
  }
  .footer .secContent .footerCard .footerIntro .footerSocials .icon:hover {
    color: var(--PrimaryColor);
  }
  .footer .secContent .footerCard .masterFooterLinks {
    font-size: 16px;
    padding: 5px 0;
  }
}
@media screen and (min-width: 2000px) {
  .footer {
    padding: 0rem;
  }
  .footer .secContent .contactDiv {
    padding-top: 1.5rem;
  }
  .footer .secContent .contactDiv .text small {
    padding: 10rem;
    font-size: 25px;
  }
}/*# sourceMappingURL=footer.css.map */